header {
  padding-top: 20px;
}

header .logo span {
  font-size: 20px;
}

header nav li:not(:last-child) {
  margin-right: 20px;
}

main h1 {
  font-size: 32px;
}

main h2 {
  font-size: 25px;
}

main .row .col:not(:last-child) {
  margin-bottom: 30px;
}

main .hero {
  margin-top: 50px;
  padding-bottom: 50px;
}

main .hero .hero-top {
  text-align: center;
}

main .hero .hero-top .col:nth-child(1) {
  order: 2;
}

main .hero .hero-top .col:nth-child(2) {
  order: 1;
}

main .hero .row:nth-child(1) {
  margin-bottom: 50px;
}

main .hero .schema {
  font-size: 0.6em;
  margin: 20px 0 70px 0;
}

main #project {
  padding: 40px 0;
}

main #project .list .col {
  background-position: 20px top !important;
}

footer {
  padding: 100px 0;
}

footer .logo {
  order: 1;
}

footer .logo img {
  width: 90px;
}

footer .logo span {
  font-size: 25px;
}

footer .left {
  order: 2;
}

footer .right {
  order: 3;
}

footer .col:not(:last-child) {
  margin-bottom: 50px;
}
